<template>
  <div class="">
    <v-row no-gutters>
      <v-col cols="4">
        <v-sheet width="300" class="mx-auto">
          <v-card loading variant="tonal" v-show="mensaje">
            <v-card-text>
              <h3>Estamos buscando un conductor, por favor espera...</h3>
            </v-card-text>
          </v-card>

          <v-divider></v-divider>
          <br />

          <div v-if="solicitudes.length == 0">
            <v-card variant="tonal">
              <v-card-text>
                <center><h2>Sin solicitudes asignadas</h2></center>
              </v-card-text>
            </v-card>
          </div>
          <div v-else>
            <v-card width="400">
              <v-card-text>
                <div class="font-weight-bold ms-1 mb-2">
                  Solicitudes asignadas
                </div>

                <v-timeline density="compact" align="start" align-top dense>
                  <v-timeline-item
                    v-for="solicitud in solicitudes"
                    :key="solicitud.id + solicitud.idCliente"
                    size="x-small"
                  >
                    <div class="mb-4">
                      <div class="font-weight-normal">
                        <strong>Datos cliente:</strong>
                      </div>
                      <div>
                        {{ solicitud.nombreCliente }} @
                        {{ solicitud.apellidoCliente }}
                      </div>

                      <div class="font-weight-normal">
                        <strong>Dirección cliente:</strong>
                      </div>
                      <div>
                        {{ solicitud.callePrincipal }} -
                        {{ solicitud.calleSecundaria }}
                      </div>

                      <div>
                        <v-avatar>
                          <v-img src="https://avataaars.io"></v-img>
                        </v-avatar>
                      </div>
                      <br />
                      <div>
                        <v-btn
                          color="primary"
                          @click="finalizarSolicitud(solicitud)"
                        >
                          Finalizar
                        </v-btn>
                      </div>
                    </div>
                  </v-timeline-item>
                </v-timeline>
              </v-card-text>
            </v-card>
          </div>
        </v-sheet>
      </v-col>
      <v-col cols="8" style="height: 600px">
        <MglMap
          :accessToken="accessToken"
          :mapStyle.sync="style"
          :center="center"
          :zoom="15"
        >
          <MglMarker :coordinates="coordinates" color="blue"> </MglMarker>
        </MglMap>
      </v-col>
    </v-row>

    <v-dialog v-model="dialog" activator="parent" width="auto">
      <v-card>
        <v-toolbar color="primary" title="Finalizar solicitud">
          <v-toolbar-title color="white"
            ><span style="color: white"
              >Finalizar solicitud conductor</span
            ></v-toolbar-title
          >
        </v-toolbar>
        <v-card-text>
          <br />
          <v-form fast-fail>
            <v-text-field
              v-model="calificacionConductor"
              label="Calificación"
              type="number"
              :rules="exampleRules"
            ></v-text-field>

            <v-text-field
              v-model="comentarioConductor"
              label="Comentario"
            ></v-text-field>

            <v-text-field
              v-model="precio"
              label="Precio"
              type="number"
            ></v-text-field>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" block @click="finalizar" :loading="loadingF"
            >Finalizar</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import Mapbox from "mapbox-gl";
import { MglMap, MglMarker } from "vue-mapbox";
import { mdiTaxi } from "@mdi/js";

export default {
  components: {
    MglMap,
    MglMarker,
  },
  data() {
    return {
      accessToken:
        "pk.eyJ1IjoibWFudWVsOTQiLCJhIjoiY2sweHlsNWozMDUzMTNucW5mdDZlOXJzNSJ9.WH8MEy5frVRM8ejqs-xebg", // your access token. Needed if you using Mapbox maps
      style: "mapbox://styles/mapbox/streets-v9",
      center: [-79.2081012, -3.9961385],
      callePrincipal: "",
      calleSecundaria: "",
      solicitudes: [],
      loading: false,
      mensajeSolicitud: "",
      mensaje: false,
      coordinates: [-79.2081012, -3.9961385],
      mdiTaxi: mdiTaxi,
      mapbox: null,
      dialog: false,
      solicitudS: [],
      calificacionConductor: "",
      comentarioConductor: "",
      precio: "",
      loadingF: false,
      exampleRules: [
        (v) => !!v || "Campo requerido",
        (v) => (v && v <= 5) || "No puede ser mayor a 5",
      ],
    };
  },
  created() {
    // We need to set mapbox-gl library here in order to use it in template
    this.mapbox = Mapbox;
  },
  methods: {
    solicitarTaxi() {
      if (this.callePrincipal === "" || this.calleSecundaria === "")
        return self.openNotification(
          "",
          2,
          "Campos Obligatorios",
          "Por favor, ingrese todos los campos"
        );
      this.loading = true;
      console.log("Solicitando Taxi");
      var self = this;

      let headersList = {
        Accept: "/",
        version: "1.0.0",
        "Content-Type": "application/x-www-form-urlencoded",
      };
      const params = new URLSearchParams();
      params.append("callePrincipal", this.callePrincipal);
      params.append("calleSecundaria", this.calleSecundaria);
      params.append("token", this.$store.state.t);
      let reqOptionsG = {
        url:
          process.env.VUE_APP_API_GETEWAY +
          ":" +
          process.env.VUE_APP_API_SOLICITAR_PORT +
          process.env.VUE_APP_SOLICITAR,
        withCredentials: true,
        credentials: "include",
        method: "POST",
        headers: headersList,
        data: params,
      };

      this.axios(reqOptionsG)
        .then(function (response) {
          console.log(response);
          if (response.data.en == 1) {
            self.loading = false;
            console.log(response);
            self.mensaje = true;
            self.openNotification(
              "",
              3,
              "Solicitud creada",
              "Estimada cliente su solicitud fue atendida con éxito, espere mientras le asignamos un conductor"
            );
            // self.solicitudes.push(response.data.conductor);
            // console.log(self.solicitudes);
          } else {
            self.loading = false;
            self.openNotification("", 2, "Error solicitud", response.data.m);
            // self.deleteSesion();
          }
        })
        .catch(function (error) {
          console.log(error);
          self.loading = false;
          self.openNotification(
            "",
            2,
            "Error solicitud",
            "No se logro generar la solicitud, por favor, inténtelo más tarde"
          );
          // self.deleteSesion();
        })
        .finally(() => {});
    },
    openNotification(position = null, color, titulo, texto) {
      var c;
      if (color === 1) c = "primary";
      else if (color == 2) c = "danger";
      else if (color == 3) c = "success";
      else if (color == 4) c = "warn";
      else c = "rgb(59,222,200)";

      const noti = this.$vs.notification({
        progress: "auto",
        color: c,
        position,
        title: titulo,
        text: texto,
      });
      console.log(noti);
    },

    finalizarSolicitud(solicitud) {
      console.log("Finalizando la solicitud");
      console.log(solicitud);
      this.dialog = true;
      this.solicitudS.push(solicitud);
    },
    finalizar() {
      console.log(this.solicitudS);
      this.loadingF = true;
      var self = this;

      let headersList = {
        Accept: "/",
        version: "1.0.0",
        "Content-Type": "application/x-www-form-urlencoded",
      };
      const params = new URLSearchParams();
      params.append("token", this.$store.state.t);
      params.append("idSolicitud", this.solicitudS[0].idSolicitud);
      params.append("calificacionConductor", this.calificacionConductor);
      params.append("comentarioConductor", this.comentarioConductor);
      params.append("precio", this.precio);
      let reqOptionsG = {
        url:
          process.env.VUE_APP_API_GETEWAY +
          ":" +
          process.env.VUE_APP_API_GETEWAY_PORT +
          process.env.VUE_APP_SOLICITAR_FINALIZAR,
        withCredentials: true,
        credentials: "include",
        method: "POST",
        headers: headersList,
        data: params,
      };

      this.axios(reqOptionsG)
        .then(function (response) {
          console.log(response);
          if (response.data.en == 1) {
            self.loading = false;
            self.loadingF = false;
            console.log(response);
            self.openNotification(
              "",
              3,
              "Solicitud finalizada",
              "Estimada conductor la solicitud fue finalizada con éxito"
            );
            console.log(self.solicitudes.length);
            for (var i = 0; i <= self.solicitudes.length; i++) {
              console.log("Eliminando la solicitud");
              if (
                self.solicitudes[i].idSolicitud ===
                self.solicitudS[0].idSolicitud
              ) {
                self.solicitudes.splice(i, 1);
                console.log("Solicitud borrada");
                self.solicitudS = [];
                self.dialog = false;
                self.calificacionConductor = "";
                self.comentarioConductor = "";
                self.precio = "";
              }
            }
          } else {
            self.loading = false;
            self.loadingF = false;
            self.openNotification(
              "",
              2,
              "Solicitud finalizada",
              "No se logro finalizar la solicitud, inténtelo más tarde"
            );
            // self.deleteSesion();
          }
        })
        .catch(function (error) {
          console.log(error);
          this.loading = false;
          self.loadingF = false;
          self.openNotification(
            "",
            2,
            "Solicitud finalizada",
            "No se logro finalizar la solicitud, inténtelo más tarde"
          );
          // self.deleteSesion();
        })
        .finally(() => {});
    },
  },
  sockets: {
    dataCliente(data) {
      console.log(
        "RESPUESTA DEL SEGVIFOR DE la data del cliente -------------------"
      );
      console.log(data);
      if (data.en == 1) {
        this.openNotification(
          "",
          3,
          "Solicitud asignada",
          "Estimado conductor, se le asignó una solicitud nueva"
        );
        this.solicitudes.push(data);
        this.mensaje = false;
      }
    },
  },
};
</script>
